@import '../../../styles/mixins';

.flash-card-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  box-sizing: border-box;
  
  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(255, 255, 255, 0.5);
    pointer-events: none;
    opacity: 0;
  }

  .card {
    border-radius: 1rem;
    border: 3px solid #CDCDCD;
    background-color: #fff; 
    height: 100%; 
    padding: 1rem;
    box-sizing: border-box;
    
    &.front {
      .image {
        width: 80%;
        height: 80%;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        margin: 10%;
      }
    }

    &.back {
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;

      .subtext {
        font-size: smaller;
      }
    }

    &.card-intro { 
      color: #6A6A6A;
  
      h2 {
        margin: 1rem;
      }
      
      display: flex;
      flex-direction: column;
  
      .intro-description {
        flex: 1;
        @include responsive-font(4vw, 6px, 20px, 50px);

      }
  
      .intro-help-correct, .intro-help-wrong {
        background-size: contain;
        position: relative;
  
        &:before {
          content: "";
          display: block;
          padding-bottom: 30%;
        }
  
        .text { 
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          @include responsive-font(4vw, 6px, 20px, 50px);
        }
      }
  
      .intro-help-correct {
        background-image: url("./../images/intro-help-correct.png");
  
        .text { 
          left: 20%;
        }
      }
  
      .intro-help-wrong {
        background-image: url("./../images/intro-help-wrong.png");
  
        .text { 
          text-align: right;
          right: 20%;
        }
      }
    }
  }


  .react-card-flip {
    width: 100%;
    height: 100%;
  }

  .react-card-front, .react-card-back {
    will-change: transform;
  }

  
}